@font-face {
	font-family: 'RobotoCondensed Light';
	src: url('../../static/font/RobotoCondensed-Light-5.ttf') format('woff2');
}
@font-face {
	font-family: 'RobotoCondensed-3';
	src: url('../../static/font/Roboto-Condensed-3.ttf') format('woff2');
}
@font-face {
	font-family: 'Aqum-2';
	src: url('../../static/font/Aqum-2-Classic-2.otf') format('woff2');
}
@font-face {
	font-family: 'Helvetica-Neue';
	src: url('../../static/font/Helvetica-Neue-2.ttf') format('woff2');
}
@font-face {
	font-family: 'LinLibertine-R-1';
	src: url('../../static/font/LinLibertine-R-1.ttf') format('woff2');
}
main {
  position: relative;
  top: -6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
  font-family: 'Helvetica-Neue';
}
.header-info{
  position: sticky;
  top: 0; 
  width: 100%;
  height: 6rem;
  margin-bottom: 1rem;
  padding: 1rem 2rem;
  box-shadow: 0 2px 20px #dcdde1;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  font-family: 'RobotoCondensed-3',Arial, Helvetica, sans-serif;
  color: #2f3542;
  background-color: rgba(255,255,255,0.7);
              
}
.header-info #title {
  font-weight: bolder;
  font-size: 3rem;
  min-width: 30rem;
}
.header-info nav {
  flex: 1;
}
.nav {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  flex-wrap: nowrap;
}
.nav a {
  text-decoration: none;
  font-size: 1.2rem;
  color: black;
  font-family: 'RobotoCondensed Light', Arial, Helvetica, sans-serif;
  font-weight: bold;
  margin-left: 1.5rem;
  padding: 1rem 0;
  transition-duration: .3s;
}
.nav a:hover{
  color: #ff4d4d;
}
.nav a {
  position: relative;
}
.nav a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 6px;
  height: 2px;
  border-radius: 2px;
  background-color: #ff4d4d;
  transition: width .3s linear;
  width: 0;
}
.nav .active {
  color: #ff4d4d;
}
.nav .active::after {
  width: 100%;
  background-color: #ff4d4d;
  display: inline-block;
}
/* 路由内容的css */
.nav-item-title {
  margin: 6rem;
  text-align: center;
  font-family: 'Helvetica-Neue';
  font-size: 3rem;
  font-weight: bold;
  color: #ff4d4d;
}
.kind-title {
  margin-bottom: 1rem;
  padding: .5rem 1rem;
  font-family: 'RobotoCondensed-3',Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
  font-weight: bold;
  color: #636e72;
  border-radius: 8px;
  background: linear-gradient(to right, #dfe4ea 0, transparent 50%);
}
.show-button {
  position: relative;
}
.mini-nav .nav{
  position: absolute;
  right: -10rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 10rem;
  background-color: rgba(255,255,255,0.7);
  translate: translateX(-10rem);
}
.mini-nav .nav li {
  padding: 1rem 0;
  margin-top: 0rem;
}


@media screen and (max-width: 600px) {
  .header-info{
    position: sticky;
    top: 0; 
  }
  .header-info #title {
    font-weight: bolder;
    font-size: 1.5rem;
    min-width: 30rem;
  }
}